* {
  box-sizing: border-box;
}

body {
  color: #212529;
  background-color: #fefae0;
  margin: 0;
  font-family: Inter, sans-serif;
  font-size: .75em;
  line-height: 1.5;
}

a {
  color: #212529;
  text-decoration: none;
}

p {
  margin: 0;
}

.search-container {
  height: 10%;
  width: 100%;
  z-index: 1;
  background-color: #ccd5ae;
  justify-content: center;
  align-items: center;
  padding-top: 10px;
  padding-bottom: 10px;
  display: flex;
  position: fixed;
  box-shadow: 2px 2px 4px rgba(33, 37, 41, .35);
}

.search-form {
  width: 100%;
  justify-content: center;
  display: flex;
}

.search-form__input {
  min-width: 30%;
  max-width: 50%;
  background-color: #fefae0;
  border: none;
  border-radius: 4px 0 0 4px;
  outline: none;
  padding: 10px;
  font-size: 1.25em;
  font-weight: 300;
}

.search-form__button {
  min-width: 4.5em;
  width: 6em;
  cursor: pointer;
  background-color: #d4a373;
  border: none;
  border-radius: 0 4px 4px 0;
  justify-content: center;
  align-items: center;
  font-size: 1.25em;
  font-weight: 500;
  display: flex;
}

.search-form__button:hover, .search-form__button:focus {
  background-color: #fefae0;
  transition: all .3s;
}

.gallery-container {
  padding-bottom: 15px;
  padding-left: 15px;
  padding-right: 15px;
}

@media screen and (min-width: 320px) {
  .gallery-container {
    width: 320px;
    margin-left: auto;
    margin-right: auto;
  }
}

@media screen and (min-width: 768px) {
  .gallery-container {
    width: 768px;
  }
}

@media screen and (min-width: 1200px) {
  .gallery-container {
    width: 1200px;
  }
}

.gallery, .info {
  flex-wrap: wrap;
  display: flex;
}

.gallery {
  justify-content: center;
  margin-left: -20px;
  padding-top: 80px;
}

.gallery-link {
  min-width: 320px;
  border: none;
  border-radius: 10px;
  flex-basis: calc(25% - 20px);
  margin-top: 20px;
  margin-left: 20px;
  transition: all .3s;
  overflow: hidden;
  box-shadow: 2px 2px 4px rgba(33, 37, 41, .35);
}

.gallery-link:hover, .gallery-link:focus {
  transform: scale(1.05);
  box-shadow: 4px 4px 8px rgba(33, 37, 41, .35);
}

.gallery-item {
  flex-direction: column;
  justify-content: space-evenly;
  display: flex;
}

.gallery-item__image {
  height: 30vh;
  object-fit: cover;
  display: block;
}

.info {
  background-color: #ccd5ae;
  justify-content: space-around;
  padding: 10px;
}

.info__item {
  flex-direction: column;
  align-items: center;
  font-weight: 500;
  display: flex;
}

.info__item:not(:first-child) {
  margin-left: 5px;
}

.info__item-name {
  font-weight: 700;
}

.load-more-btn.is-hidden {
  transform: all .3s;
  display: none;
}

.load-more-btn {
  min-width: 8em;
  color: #212529;
  background-color: #d4a373;
  border: none;
  border-radius: 5px;
  margin: 30px auto 0;
  padding: 10px;
  font-size: 1.25em;
  font-weight: 500;
  display: block;
  box-shadow: 2px 2px 4px rgba(33, 37, 41, .35);
}

.load-more-btn:hover, .load-more-btn:focus {
  transform: scale(1.05) .3s;
  box-shadow: 2px 2px 4px rgba(33, 37, 41, .35);
}

